import React, { Component } from 'react';
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";
import User from './User.js';
import axios from 'axios';
import './App.css'; 
import ComboCreator from './ComboCreator.js';
import ComboViewer from './ComboViewer.js';



// export default RoutePage;


class Routeth extends Component { 
    constructor(props){ 
      super(props); 
      this.state = { 
        username:'', 
        password:''
      }
    }
    

    logOut=()=>{ 
        this.props.logOut();
    }
  
      render() {
        return ( 
            <Router> 
                <div> 
                    <div className="header">               
                        <NavLink className="logo" to="/">George's PLL Trainer: Welcome {this.props.currentUsername}</NavLink> 
                        <button onClick={this.logOut}>Logout</button>
                        <div className="header-right">   
                        
                            
                            {/* <NavLink to="/">Users</NavLink> */}
                            <NavLink to="/">Hello</NavLink>
                            {/* <NavLink to="/ComboViewer">Die Cunt</NavLink> */}
                            <NavLink className="logo" to="/">George's PLL Trainer: Welcome {this.props.currentUsername}</NavLink> 
                        <button onClick={this.logOut}>Logout</button>

                            
                            <Route exact path="/" component={ComboCreator}></Route>        
                            {/* <Route path="/ComboCreator" component={ComboCreator}></Route>           */}
                            <Route path="/ComboViewer" component={ComboViewer}></Route>    
                        </div>
                    </div>
                </div>        
            </Router> 
       
        );
      } 
    }
    
    export default Routeth;